/* eslint-disable no-undef */
import React from 'react'
import './Feedback.css'
function Feedback() {
    return (
        
        <div className="container">
            
              <form id="submit-form" method="post" action="https://script.google.com/macros/s/AKfycbwcV4_G3F0QmgCNkHQDzHAwt2FVBgkzQRcNGPn-KtPvuNdo2olWcKBLz0K0p4OlYKyc/exec">
                  
              Feedback <br/>
              <textarea id="subject" name="feedBack" placeholder="Write something.." ></textarea><br/>
              <input 
              onClick={() => {
                            alert('Enter ok it will submit')
                            }
                        }
              
              type="submit" value="Submit"></input><br/>
              </form>


        </div>
    )
}

export default Feedback
